<template>
  <div>
    <el-upload
      class="mb-2"
      ref="upload"
      :action="url"
      :data="file"
      :headers="headers"
      :before-upload="handleBeforeUpload"
      :on-preview="handlePreview"
      :on-change="handleChange"
      :on-progress="handleProgress"
      :on-success="handleSuccess"
      :on-remove="handleRemove"
      :on-error="handleError"
      :auto-upload="false"
      :show-file-list="true">
      <el-button slot="trigger" size="small" type="primary">Select file</el-button>
    </el-upload>
    <el-table
      :data="fileList"
      size="small"
      border
      style="width: 100%">
      <el-table-column type="index" width="35" align="center" fixed="left" />
      <el-table-column
        v-for="(c, i) in columnDefs"
        :key="i"
        :label="c.label"
        :width="c.width"
        :fixed="c.fixed"
        :prop="c.prop"
        :sortable="true">
      </el-table-column>
      <el-table-column
        fixed="right"
        label="Operations"
        width="120">
        <template slot-scope="scope">
          <el-button
            type="text"
            size="small"
            class="danger--text"
            @click.native.prevent="removeFile">
            Remove
          </el-button>
          <el-button
            type="text"
            size="small"
            class="danger--text"
            @click.native.prevent="uploadFile">
            Upload
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- <el-progress :percentage="progress" :status="status" :color="customColors" /> -->
  </div>
</template>

<script>
export default {
  name: 'RMCUpload',

  data: () => ({
    // Progress
    customColors: [
      {color: '#f56c6c', percentage: 20},
      {color: '#e6a23c', percentage: 40},
      {color: '#6f7ad3', percentage: 60},
      {color: '#1989fa', percentage: 80},
      {color: '#5cb87a', percentage: 100}
    ],
    progress: 0,
    status: null,
    // Request
    url: process.env.RMC_API + '/upload',
    file: {},
    headers: {
      Authorization: 'Bearer eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..yjik4R2ZXy2HV1S1a21D_XxxPwHQ7RDI02KqQZaatE4'
    },
    // files
    fileList: [],
    // Table
    columnDefs: [
      { label: 'File name', prop: 'name' },
      { label: 'Segment', prop: 'segment' },
      { label: 'Upload by', prop: 'upload_by' },
      { label: 'Upload at', prop: 'upload_at' },
      { label: 'Size', prop: 'size' },
      { label: 'Status', prop: 'status' },
      { label: 'Records', prop: 'total' },
      { label: 'Pass', prop: 'pass' },
      { label: 'Fail', prop: 'fail' }
    ],
    tableData: []
  }),

  methods: {
    handlePreview(file) {
      // console.log(file)
    },

    handleChange(file, fileList) {
      this.fileList.push(file)
      console.log(this.fileList)
    },

    handleBeforeUpload(file) {
      console.log(file)
    },

    handleProgress(event, file, fileList) {
      this.progress = file.percent
    },

    handleSuccess(response, file, fileList) {
      // console.log(file)
    },

    handleRemove(file, fileList) {
      this.fileList.splice(this.fileList.findIndex(f => f.uid === file.uid), 1)
    },

    handleError(err, file, fileList) {
      console.log(err)
    },

    uploadFile() {
      this.$refs.upload.submit()
    }
  }
}
</script>



